/* eslint-disable jsx-a11y/media-has-caption */
import { useEffect } from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { useLocation } from 'react-router-dom';
import AOS from 'aos';
import Style from './Style.module.scss';
import Header from './Header';
import Footer from './Footer';
import IndexBg from '../../../assets/index_bg.mp4';
import 'aos/dist/aos.css';

const LayoutV2 = ({ children }) => {
  const location = useLocation();

  useEffect(() => {
    console.log('init', document.querySelector('#sContainer'));
    AOS.init({
      duration: 800,
      once: true,
    });
  }, []);

  return (
    <div className={Style.Main}>
      <Header />
      <div className={Style.Content}>
        <TransitionGroup component={null} className={Style.fadeContainer}>
          <CSSTransition
            key={location.pathname}
            timeout={100}
            classNames={{
              enter: Style['fade-enter'],
              enterActive: Style['fade-enter-active'],
              exit: Style['fade-exit'],
              exitActive: Style['fade-exit-active'],
            }}>
            {/* {children} */}
            <div className={Style.TransitionContainer} id="sContainer">
              {children}
              <Footer />
            </div>
          </CSSTransition>
        </TransitionGroup>
      </div>

      <div className={Style.Background}>
        <video src={IndexBg} autoPlay loop muted className={Style.BackgroundVideo} />
      </div>
    </div>
  );
};

export default LayoutV2;
