import { useState } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import { Menu, Dropdown } from 'antd';
import Style from './Style.module.scss';
import { menu } from './constants';
import Logo from '../../../assets/images/logo.png';
import Hamburger from '../../../assets/images/hamburger.png';

const renderMenu = ({ menus, history, location }) => (
  <Menu>
    {menus.map((item) => (
      <Menu.Item>
        <div
          onClick={() => history.push(item.path)}
          className={`${Style.HeaderNavItem} ${
            location.pathname === item.path ? Style.MenuActive : ''
          }`}
          key={item.path}>
          {item.name}
        </div>
      </Menu.Item>
    ))}
  </Menu>
);

const renderFirstMenu = ({ menus, history, location }) => {
  const isCurrent = (path) => location.pathname.includes(path);

  return menus.map((item) => (
    <div
      key={item.name}
      onClick={(e) => {
        e.stopPropagation();
        history.push(item.path);
      }}>
      <div className={Style.HamburgerNavItem}>
        {item.name}
        <div
          className={Style.HamburgerNavItemLine}
          style={{ visibility: isCurrent(item.path) ? 'visible' : 'hidden' }}
        />
      </div>
      {item.children && renderFirstMenu({ menus: item.children, history, location })}
    </div>
  ));
};

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const location = useLocation();

  const history = useHistory();

  return (
    <div className={Style.Header}>
      <div className={Style.HeaderContainer}>
        <div className={Style.HeaderLeft} onClick={() => history.push('/')}>
          <img src={Logo} alt="gfu" />
        </div>

        <div className={Style.HeaderCenterNav}>
          {menu.map((item) =>
            item.children ? (
              <Dropdown
                overlay={renderMenu({
                  menus: item.children,
                  history,
                  location,
                })}
                trigger={['hover']}>
                <div
                  onClick={() => history.push(item.path)}
                  className={`${Style.HeaderNavItem} ${
                    location.pathname.includes(item.path) ? Style.MenuActive : ''
                  }`}
                  key={item.path}>
                  {item.name}
                </div>
              </Dropdown>
            ) : (
              <div
                onClick={() => history.push(item.path)}
                className={`${Style.HeaderNavItem} ${
                  location.pathname === item.path ? Style.MenuActive : ''
                }`}
                key={item.path}>
                {item.name}
              </div>
            ),
          )}
        </div>

        <div className={Style.HeaderRightNav}>
          <img
            className={Style.Hamburger}
            src={Hamburger}
            alt="gfu"
            onClick={() => {
              setIsMenuOpen(!isMenuOpen);
            }}
          />
          {isMenuOpen && (
            <div className={Style.HamburgerNav}>
              {renderFirstMenu({ menus: menu, history, location })}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Header;
