import { useParams } from 'react-router-dom';
import { useMemo } from 'react';
import Button from '../../components/Button/Button';
import Style from './Index.module.scss';
import Head1 from '../../../../assets/images/head_1.png';
import Head2 from '../../../../assets/images/head_2.png';
import Line from '../../../../assets/images/line.png';

const Advocates = () => (
  <>
    <div className={Style.Text}>Begin my fundraising journey like</div>
    <div className={Style.Title}>Advocates</div>
    <div className={Style.Button}>
      <Button type="primary">Sign up</Button>
    </div>
  </>
);

const Volunteer = () => (
  <>
    <div className={Style.Text}>Begin my fundraising journey like</div>
    <div className={Style.Title}>Volunteer</div>
    <div className={Style.Button}>
      <Button type="primary">Sign up</Button>
    </div>
  </>
);

const Index = () => {
  const params = useParams();

  const isAdvocate = useMemo(() => params.type === 'advocate', [params.type]);

  return (
    <div className={Style.Volunteer}>
      <div className={Style.Card}>
        <div className={Style.CardLine}>
          <img src={Line} alt="gfu" />
        </div>
        <div className={Style.CardHead}>
          <img className={Style.CardHeadImage} src={isAdvocate ? Head1 : Head2} alt="gfu" />
        </div>
        {isAdvocate ? <Advocates /> : <Volunteer />}
      </div>

      <div className={Style.CardDesc}>
        {isAdvocate ? (
          <span>
            GoFundUs Advocates provide Proof-of-validation(PoV), while they can chose whether their
            services earnings are donated or not, by giving time and efforts for GoFundUs services.
          </span>
        ) : (
          <span>
            GoFundUs Volunteers provide Proof-of-Validation(PoV), while their services earnings are
            donated, by giving time and efforts for GoFundUs services.
          </span>
        )}
      </div>
    </div>
  );
};

export default Index;
