import React from 'react';
import PropTypes, { any } from 'prop-types';
import { useTranslation } from 'react-i18next';
import classNames from 'classnames';

import styles from './ProjectsCard.module.scss';

const ProjectsCard = (props) => {
  const { projectInfo, className } = props;
  const { src, title, desc } = projectInfo;
  const { t } = useTranslation();
  return (
    <div className={classNames(styles.daoIntroCard, className)}>
      <img src={src} alt={`${t(title)} icon`} />
      <h3>{t(title)}</h3>
      <p>{t(desc)}</p>
    </div>
  );
};

ProjectsCard.propTypes = {
  projectInfo: PropTypes.objectOf(any).isRequired,
  className: PropTypes.string,
};
ProjectsCard.defaultProps = { className: '' };

export default ProjectsCard;
